import React from 'react';
import { Grid, Card, Typography, TextField, Button  } from 'material-ui';
import { createTheme, ThemeProvider } from 'material-ui';
import image from '../assets/login-bg.png';

const themeProvider = createTheme({
  components: {
    MuiTextField: {
      styleOverrides: {
        root: {
          background: '#EDF2F7',
          borderRadius: 20,
          '& .MuiOutlinedInput-root': {
            borderRadius: 20,
            '& fieldset': {
              borderColor: '#EDF2F7',
            },
            '&:hover fieldset': {
              borderColor: '#555',
            },
            '&.Mui-focused fieldset': {
              borderColor: 'white',
            },
          },
        },
      },
    },
  },
});

export default function Root(props) {

  const [username, setUsername] = React.useState('');

  React.useEffect(() => {
    document.getElementById('mui-nav-main').style.width = '100%'
  }, [])
  
  const login = () => fetch('http://localhost:3000/helios/auth-login?key=' + username, {
    method: "GET",
    redirect: 'follow',
    headers: {
      "Content-Type": "application/json"
    },
  }).then(d => d.text()).then(d => location.href = 'http://localhost:9000' + d )

  return (<ThemeProvider theme={themeProvider}>
    <Grid container style={{height: 85}}>
      <Grid md={7} style={{ padding: 0, paddingRight: 10, height: '100vh' }}>
        <Card elevation={2} style={{border: '1px solid #e4e4e4', height: '90vh'}}>
          <Grid container justifyContent="center">
            <Grid item>
              <img src={image} style={{height: '90vh'}} alt="image" />
            </Grid>
          </Grid>
        </Card>
      </Grid>
      <Grid md={5} style={{ height: '100vh', paddingLeft: 5 }}>
        <Card elevation={2} style={{border: '1px solid #e4e4e4', height: '90vh', paddingTop: 10, paddingLeft: 10, paddingRight: 10}}>
          <Grid container justifyContent="space-between" >
            <Grid container md={12} style={{paddingTop: '30%'}} justifyContent="flex-start" alignItems="center">
              <Grid item md={12} style={{padding: 5}}>
                <TextField fullWidth placeholder="Username" value={username} onChange={e => setUsername(e.target.value)} />
              </Grid>
              <Grid item md={12} style={{padding: 5}}>
                <TextField fullWidth placeholder="Password"/>
              </Grid>
            </Grid>
            <Grid container md={12} justifyContent="flex-end" alignItems="center" style={{padding: 5}}>
              <Grid item >
                <Button disabled={!username} style={{background: '#005093', borderRadius: 25}} variant="contained" onClick={() => login()}>
                  LOGIN
                </Button>
              </Grid>
              <Grid item >
              </Grid>
            </Grid>
          </Grid>
        </Card>
      </Grid>
    </Grid>
  </ThemeProvider>);
}
